import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { snavarsActionCreator } from '../sna_actions';
import  {SwatchesPicker} from 'react-color';
import * as d3 from 'd3';

const ModalLinkcolor = () => {
	
	const SNAstate = useSelector((state) => state.snavars);
	//const {nodecolor, linkcolor, highlight} = SNAstate;

	const dispatcher = useDispatch();
	
	const changeVal = (e) => {
	
		d3.selectAll(".snalinks")._groups[0].forEach(lk => {
			lk.attributes.style.value = "stroke: "+e.hex;
		})
	
		dispatcher(snavarsActionCreator({
						refresh: SNAstate.refresh,
						nodeColor:SNAstate.nodeColor, 
						linkColor:e.hex, 
						isHighlight: SNAstate.isHighlight
				}))
	};

	return (
		<div className="modalPopup">
			Click to Change Link Color
			<SwatchesPicker onChangeComplete={e=> changeVal(e)}/>
		</div>
	)
};

export default ModalLinkcolor;