import React from 'react';
import {useSelector} from 'react-redux';
import * as d3 from 'd3';


const ModalScores = () => {

	const snalinks = useSelector((state) => state.sna_links);

	const ScoreColors = () => {
		snalinks.forEach(lk => {
				let line = d3.select("#"+lk['source']+"--"+lk['target'])._groups[0][0]

				let range = [4,5]
				let bg1 = Math.min( ((lk.value1-range[0])/(range[1]-range[0]))*255, 255)
				let bg2 = Math.max(255 - ((lk.value1 - range[0])/(range[1]-range[0]))*255, 0)
				
				line.attributes.style.value = "stroke: rgb(0," + bg1 + "," + bg2 +")"
		})
			
	};

	return (
		<div className="modalPopup">
			Apply score colors to links
			<button onClick={ScoreColors} > Scores </button>
		</div>
	)
};

export default ModalScores;