import React, {useState, ChangeEvent} from 'react';
import axios from 'axios';
import { empdataActionCreator } from './data_actions';
import { useDispatch } from 'react-redux';
import {Emp} from "../../type";
 
const DataImport = () => {

	const [formData, setFormData] = useState({
		location: 'N/A',
	});
	const { location } = formData;
	const dispatcher = useDispatch();
	const [empData, setempData] = useState<Emp[]>([]);

	const handleFormChange = (e: ChangeEvent<HTMLInputElement>): void => {
		setFormData({location: e.target.value});
	};

	//------using POST
    const dataretrieve_post = () => {
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        };
		//axios call
        axios.post<Emp[]>(`${process.env.REACT_APP_API_URL}/api/data/datapost`, {location}, config)
        .then(res => {

		    setempData(res.data);
			dispatcher(empdataActionCreator(empData[0]));
			
			
		   //const {name, empid, location} = res.data[0]
		  // const linedata = {name:"",empid:"",location:""}
		   //dispatcher(empdataActionCreator(linedata));
        })
        .catch(err => {
			console.log('data error');
        })
    };
	
	//------using GET
    const dataretrieve_get = () => {
		const config = {
			headers: {
				'Authorization': `Bearer ${localStorage.getItem('token')}`
			}
		};

		//axios call
        axios.get<Emp[]>(`${process.env.REACT_APP_API_URL}/api/data/data`, config)
        .then(res => {
           console.log(res.data);
		   /* const {name, empid, location} = res.data.results[0]
		   const linedata = {name:"",empid:"",location:""}
		   
		   dispatcher(empdataActionCreator(linedata)); */
        })
        .catch(err => {
			console.log('data error');
        })
    };

    return (
       <div>
		   Data Page

			  <label> Location </label>
			  <input
				onChange={handleFormChange}
				value={formData.location}
			  />
			  <button onClick={dataretrieve_post}>Post</button>
			  <button onClick={dataretrieve_get}>Get</button>

	   </div>
    );
};


export default DataImport;
