import * as d3 from 'd3';


const Pathfind = (links, selectednode, secondarynode, maxdepth) => {
	
	//locations of end node
	let secondaryNodeLoc = d3.select("#" + secondarynode)._groups[0]
	let ex = secondaryNodeLoc[0].__data__.x;
	let ey = secondaryNodeLoc[0].__data__.y;	

	//single iteration - create list of adjacent node IDs
	const linkloop = (id, pathIDs, endpoint) => {
		let newpaths = [];
		let neighborList = []; //prevent duplicate pathing
		let foundAPath = false	
		
		if(pathIDs[pathIDs.length - 1]!==endpoint) { //path hasn't ended, iterate
			
			links.forEach(lk => {
				let trailingpath = [...pathIDs]
					if (lk.source === id & pathIDs.indexOf(lk.target)===-1 & neighborList.indexOf(lk.target)===-1){
						trailingpath.push(lk.target)
						newpaths.push(trailingpath)
						neighborList.push(lk.target)
						foundAPath = true
					}
					if (lk.target === id & pathIDs.indexOf(lk.source)===-1 & neighborList.indexOf(lk.source)===-1){
						trailingpath.push(lk.source)
						newpaths.push(trailingpath)
						neighborList.push(lk.source)
						foundAPath = true
					}
			})
		}
		if (!foundAPath){ return [pathIDs] }
		return(newpaths)
	}
	
	//after iterations, trim out paths
	const trimPath = (paths, iter) => {
		let exp_paths = []

			paths.forEach(pl => {
					if (pl.length > iter - 3){ //dead end
						if (pl.length >= 5 & paths.length > 100){
							//must be getting closer within last 5 steps
							try {
								let bx = d3.select("#" + pl[pl.length-5])._groups[0][0].__data__.x;
								let by = d3.select("#" + pl[pl.length-5])._groups[0][0].__data__.y;
											
								let x = d3.select("#" + pl[pl.length-1])._groups[0][0].__data__.x;
								let y = d3.select("#" + pl[pl.length-1])._groups[0][0].__data__.y;
								
								let starting_dist = Math.pow(Math.pow(bx - ex,2)+Math.pow(by - ey,2),.5)
								let dist = Math.pow(Math.pow(x - ex,2)+Math.pow(y - ey,2),.5)
								
								if (dist < starting_dist){ //is closer, pass to next
									exp_paths.push(pl);
								}
							} catch (e) {
								console.log("Cant find")
								console.log(pl[pl.length - 5])
								console.log(pl[pl.length - 1])
							}
							
						} else {
							//other criteria (too short) - pass through
							if ( pl.length < 6 | paths.length <= 100  | pl[pl.length-1]===secondarynode) { 
								exp_paths.push(pl) 
							};
						};
					};	
			});
		return(exp_paths)
	}

	let path_list = [];	
	let foundpaths = 0
	path_list.push(selectednode);
	path_list = linkloop(selectednode,path_list);
	//console.log(path_list);
	
	 for (let m = 0; m <= maxdepth; m++){
		let newpaths = [];
		if (foundpaths < 15){
			path_list = path_list.map((p,i) => {
									let nextpaths = linkloop(p[p.length-1], p, secondarynode);
									if (nextpaths.length===1){
										nextpaths = nextpaths[0];
									};
									if (typeof(nextpaths[0])==="object"){
										nextpaths.forEach(np => {
											newpaths.push(np);
										})
									} else {
										newpaths.push(nextpaths);
									};
									return(nextpaths);
								});
			path_list = newpaths;	
			path_list = trimPath(path_list, m)
												
			path_list.forEach(p => {
				if (p[p.length-1]===secondarynode){
					foundpaths += 1
				}
			});
		}
	} 
	
	//trim down on paths leading elsewhere
	let paths = [];
	path_list.forEach(pth => {
		if (pth[pth.length-1]===secondarynode){
			paths.push(pth);
		}
	});

	paths = paths.sort(function(a,b){
				return a.length - b.length;
			})
	
	paths = paths.slice(0,15)
	
	console.log(paths);

	
	return(paths); 

};

export default Pathfind;