import SNAImport from '../SNAImport';
import {useSelector} from 'react-redux';

const ModalImport = () => {

	const snavars = useSelector((state) => state.snavars);	
	const snanodes = useSelector((state) => state.sna_nodes)
	const snanodesfull = useSelector((state) => state.sna_fullnodes)
	const snalinks = useSelector((state) => state.sna_links)
	const snalinksfull = useSelector((state) => state.sna_fulllinks)
	const reviews = useSelector((state) => state.reviewdata)

	return (
       <div className="modalPopup">
			<SNAImport />
			<br />
			
			<div> Model Facts: </div>
			<div> # Nodes: {snanodesfull.length} </div>
			<div> # Links: {snalinksfull.length} </div>
			<div> # Reviews: {reviews.length} </div>
			<div> # Distinct Clusters: {snavars.numClust} </div>
			<div> Filtered Nodes: {snanodes.length} </div>
			<div> Filtered Links: {snalinks.length} </div>
			Items: 

	   </div>
    );
};


export default ModalImport;
