import React, { FormEvent, ChangeEvent, useState } from 'react';
//import { Link } from 'react-router-dom';
import {useDispatch} from 'react-redux';
//import {useSelector} from 'react-redux';
import { loginActionCreator } from '../auth/user_actions';
//import { State } from "../type";
import axios from 'axios';

const Login = () => {

	//react-redux functions
	const dispatcher = useDispatch();
	//const user = useSelector((state: State) => state.user );
	
	
	const [formData, setFormData] = useState({
        email: '',
        password: ''
    });
	
    const { email, password } = formData;

    const onChange = (e: ChangeEvent<HTMLInputElement>):void => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};
    
	const onSubmit = (e: FormEvent<HTMLFormElement>):void => {
        e.preventDefault();
        dispatcher(loginActionCreator({email:email, password: password}));

		loginauth();
    };
	
	const loginauth = () => {	
	//authentication - move to separate file
		const config = {
			headers: {
				'Content-Type': 'application/json',
			}
		};
		const body = JSON.stringify({ email, password });
		try {
			const res =  axios.post(`${process.env.REACT_APP_API_URL}/auth/jwt/create`, body, config);
			console.log(res)
		} catch (err){
			
		}

	}

	
	
	return(
		<div>
		  <form className='auth-form' onSubmit={e => onSubmit(e)}>
                <div className='auth__form__group'>
                    <input 
                        className='auth__form__input'
                        type='email'
                        placeholder='Email'
                        name='email' 
						value={email}
                        onChange={e => onChange(e)}
                        required
                    />
                </div>
                <div className='auth-form'>
                    <input
                        className='auth__form__input'
                        type='password'
                        placeholder='Password'
                        name='password'
                        value={password}
                        onChange={e => onChange(e)}
                        minLength={6}
                    />
                </div>
                <button className='auth__form__button'>Login</button>
            </form>
		</div>
	)
	
};

export default Login;