import {todoSlice, selectedTodoSlice, counterSlice} from './redux-toolkit'
import {configureStore, getDefaultMiddleware} from '@reduxjs/toolkit';
//import {logger} from 'redux-logger';
import {userSlice} from './auth/user_actions'
import {empdataSlice} from './pages/data/data_actions'
import {snavarsSlice, reviewDataSlice, bigramDataSlice, 
		linksSlice, nodesSlice, linksFullSlice, nodesFullSlice, pathSlice} from './pages/sna/sna_actions'



//combines reducers - toolkit will understand object
const reducer = {
	todos: todoSlice.reducer,
	selectedTodo: selectedTodoSlice.reducer,
	counter: counterSlice.reducer,
	user: userSlice.reducer,
	empdata: empdataSlice.reducer,
	reviewdata: reviewDataSlice.reducer,
	bigramdata: bigramDataSlice.reducer,
	snavars: snavarsSlice.reducer,
	sna_nodes: nodesSlice.reducer,
	sna_fullnodes: nodesFullSlice.reducer,
	sna_links: linksSlice.reducer,
	sna_fulllinks: linksFullSlice.reducer,
	sna_paths: pathSlice.reducer,
};


//store: comes with redux-thunk & devtools
export default configureStore({
	reducer,
	middleware: [...getDefaultMiddleware(), 
				//logger
				]
});
