export const snavarsSlice = createSlice({
	name: 'snavar_action',
	initialState: snaInit,
	reducers: {
		model_select: (state, {payload}: PayloadAction<{modelName: string}>) => {
			state.modelName = payload.modelName
		},
		load_vars: (state, {payload}: PayloadAction<{refreshData: boolean, 
																refreshVars: boolean, 
																nodeColor: string, 
																linkColor: string, 
																isHighlight: boolean,
																selectedNode: string,
																}>) => {
				//immer import allows mutation of state (redux toolkit only)
				state.refreshData = payload.refreshData;
				state.refreshVars = payload.refreshVars;
				state.nodeColor = payload.nodeColor;
				state.linkColor = payload.linkColor;
				state.isHighlight = payload.isHighlight;
		},
		refresh_data: (state, {payload}: PayloadAction<{refreshData: boolean}>) => {
			state.refreshData = payload.refreshData
		},
		refresh_var: (state, {payload}: PayloadAction<{refreshVars: boolean}>) => {
			state.refreshVars = payload.refreshVars
		},
		selected_var: (state, {payload}: PayloadAction<{selectedNode: string}>) => {		
			state.selectedNode = payload.selectedNode
		},
		secondary_sel_var: (state, {payload}: PayloadAction<{secondaryNode: string}>) => {
			state.secondaryNode = payload.secondaryNode
		},
		path_select: (state, {payload}: PayloadAction<{pathInt: number}>) => {
			state.pathInt = payload.pathInt
		},
		reset_values: (state, {payload}: PayloadAction<{}>) => {
			state.pathInt = 0;
			state.selectedNode = '';
			state.secondaryNode = '';
		},
		num_cluster: (state, {payload}: PayloadAction<{numClust: number}>) => {
			state.numClust = payload.numClust;
		},
		item_select: (state, {payload}: PayloadAction<{selectedItem: string}>) => {
			state.selectedItem = payload.selectedItem;
		},
	}
});

//reviews
export const reviewDataSlice = createSlice({
	name: 'reviewdata_action',
	initialState: reviewInit,
	reducers: {
		reviewdata: {
			reducer: (state, {payload}: PayloadAction<Review>) => {
				state.push(payload);
			},
			prepare: ({ModelKey, ReviewItem, Summary, Description, Rating, Topic, TopicScore}: Review) => ({ 
				payload: {
					ModelKey: ModelKey,
					ReviewItem: ReviewItem,
					Summary: Summary,
					Description: Description,
					Rating: Rating,
					Topic: Topic,
					TopicScore: TopicScore,
				}
			})			
		},
		review_delete: (state, {payload}: PayloadAction<{}>) => {
			state.splice(0,state.length);
		},
	}
});

//review bigrams
export const bigramDataSlice = createSlice({
	name: 'bigramdata_action',
	initialState: bigramInit,
	reducers: {
		bigramdata: {
			reducer: (state, {payload}: PayloadAction<ReviewBigram>) => {
				state.push(payload);
			},
			prepare: ({id, ModelKey, Bigram_Term1, Bigram_Term2, Score, Distance, Count_Term1, Count_Term2, Term_Num1, Term_Num2}:
						ReviewBigram) => ({ //prepares payload allowing custom functions
				payload: {
						id: id,
						ModelKey: ModelKey,
						Bigram_Term1: Bigram_Term1,
						Bigram_Term2: Bigram_Term2,
						Score: Score,
						Distance: Distance,
						Count_Term1: Count_Term1,
						Count_Term2: Count_Term2,
						Term_Num1: Term_Num1,
						Term_Num2: Term_Num2,
				}
			})			
		},
		bigram_delete: (state, {payload}: PayloadAction<{}>) => {
			state.splice(0,state.length);
		},
	}
});

