//links - from bigrams
export const linksSlice = createSlice({
	name: 'links_action',
	initialState: linksInit,
	reducers: {
		createlinks: {
			reducer: (state, {payload}: PayloadAction<SNA_Link>) => {
				state.push(payload);
			},
			prepare: ({modelid, id, source, target, cluster, value1, value2}:
						SNA_Link) => ({ //prepares payload allowing custom functions
				payload: {
						modelid: modelid,
						id: id,
						source: source,
						target: target,
						cluster: cluster,
						value1: value1,
						value2: value2,
				}
			})			
		},
		deletelinks: (state, {payload}: PayloadAction<{}>) => {
			//delete all
			state.splice(0,state.length);
		}
	}
});

