
const Clusters = (nodes, links) => {
	
	let nodegroups = [];
	let nodenames = [];

	nodes.forEach((nd,i) => {
		nodenames.push(nd['Name'])
		nodegroups.push(i)
	});
	
	links.forEach(lk => {
		let s_int = nodenames.indexOf(lk['Bigram_Term1'])
		let t_int = nodenames.indexOf(lk['Bigram_Term2'])
		let changefromval = nodegroups[t_int]
		
		nodegroups.forEach((ng,k) => {
			if (ng === changefromval){
				nodegroups[k] = nodegroups[s_int];
			}
		});
	});

	let clusterids = [];
	nodegroups.forEach((ng,i) => {
		if (clusterids.indexOf(ng)===-1){
			clusterids.push(ng)
		}
	}); 
	let clustercounts = clusterids.map(c => 0);
	nodegroups.forEach(ng => {
		clustercounts[clusterids.indexOf(ng)] += 1
	});
	let clustermap = clusterids.map((clust,c) => {
		return { id: clust, count: clustercounts[c] }
	}).sort(function(a,b){
		return b.count - a.count
	});
	let clustersortedIDs = clustermap.map(c => {
		return c.id
	})
	
	let finalgroups = [];	
	nodes.forEach((nd,i) => {
		let intg = clustersortedIDs.indexOf(nodegroups[i])
		finalgroups.push({id: nd.Name, cluster: intg})
	});

	return(finalgroups)
};

export default Clusters;