import React from 'react';
import  {BrowserRouter, Route, Switch } from 'react-router-dom';

//import Layout from './hocs/Layout'
import Home from './pages/home';
import Dashboard_Home from './pages/dashboard/Dashboard_Home';
import Login from './pages/login';
import ToolkitApp from './ToolkitApp';
import DataImport from './pages/data/DataImport';
import ReviewImport from './pages/data/ReviewImport';
import SNABase from './pages/sna/SNABase';


/*import Login from './pages/Login';
import Signup from './pages/Signup';
import Activate from './pages/Activate';
import ResetPassword from './pages/ResetPassword';
import ResetPasswordConfirm from './pages/ResetPasswordConfirm';
 */
//import ErrorBoundary from './errorboundary';


function App() {
  return (
		<BrowserRouter >
				<Switch>
					<Route exact path='/home' component={Home}/>
					<Route exact path='/toolkit' component={ToolkitApp}/>
					<Route exact path='/data' component={DataImport}/>
					<Route exact path='/login' component={Login}/>
					<Route exact path='/review' component={ReviewImport}/>
					<Route exact path='/sna' component={SNABase}/>
					<Route exact path='/dashboard' component={Dashboard_Home}/>
				</Switch>
		</BrowserRouter>
  );
}

export default App;


