import {useSelector} from 'react-redux';
import * as d3 from 'd3';


const ModalNodeDropdown = () => {
	
	const snanodes = useSelector((state) => state.sna_nodes);
	//const dispatcher = useDispatch();
	
	const centernode = (e) => {
		try {
		let nodeid = snanodes[e.target.selectedIndex].id;		
		let nodeSelect = d3.select("#"+nodeid)._groups[0][0]
		
		let cx = +nodeSelect.attributes.cx.value  - 850/2
		let cy = +nodeSelect.attributes.cx.value  - 750/2
		d3.select("#g_focus")._groups[0][0].attributes.transform.value = "translate(" + -cx + "," + -cy + ")"
		d3.select("#"+nodeid).attr('fill','blue');
		} catch(err){
			console.log('node not found');
		}
	};
		
	const Nodedropdown = () => {
		
		if (snanodes.length > 1){
			return(
				<div>
					<select onChange = {e => centernode(e)}>
						{
							snanodes.map((d,i) => {
								return(
									<option key={"b"+i} value = {d['id']} >
										{d.id}
									</option>
								)
							})
						}
					</select>
				</div>
			);
		} else {
			return(<div></div>);
		}
	};
	
	return(
			<div className="modalPopup">
				<Nodedropdown />
			</div>
		)
};

export default ModalNodeDropdown;