import * as d3 from 'd3';
import {useEffect} from 'react';
import {useSelector, useDispatch } from 'react-redux';
import {snavarsActionCreator, snaselectActionCreator, 
		pathdeleteActionCreator, resetvalsActionCreator} from './sna_actions';

const Network2D = () => {
	
	let dispatcher = useDispatch();
	let SNAstate = useSelector((state) => state.snavars);

	//before creating nodes via backend
		const getNodeNames = (data, linkfieldName1, linkfieldName2) => {
			let new_nodes = [];
			let uniquevals = [];
			data.forEach(d => {
				if (uniquevals.indexOf(d[linkfieldName1])===-1){
					uniquevals.push(d[linkfieldName1])
					new_nodes.push({name: d[linkfieldName1], id: d[linkfieldName1], cluster: d['cluster']})
				};
				if (uniquevals.indexOf(d[linkfieldName2])===-1){
					uniquevals.push(d[linkfieldName2])
					new_nodes.push({name: d[linkfieldName2], id: d[linkfieldName2], cluster: d['cluster']})
				};
			});
			return new_nodes;
		};
	let links = useSelector((state) => state.sna_links);
	let nodes = getNodeNames(links,"source","target");
	//let nodes = useSelector((state) => state.sna_nodes);
	
	//code begin
	useEffect(() => {
		if (SNAstate.refreshData){
			console.log('refresh sna');
			d3.selectAll("#sna_svg").remove()

			svg = d3.select("#SVGContainer")
						.append("svg")
						.attr("id","sna_svg")
						.attr("width", width)
						.attr("height", height)
						.attr("style", "outline: thin solid black");

			g = svg.append("g").attr("id","g_focus").attr("transform","translate(0,0)"); //requried to prevent zoom/drag from crashing svg parent
			
			fixLinkformat();
			initializeSimulation();
			rerun();
			
			//apply defaults
			circles.style("stroke",SNAstate.nodeColor).attr("stroke-width", 2)
			link.style("stroke",SNAstate.linkColor)		
		};
	});

	//edit data to be readable by force network
	const fixLinkformat = () => {
		Revlinks = [];
		links.forEach((ln) => {
			Revlinks.push({id: ln.source + "-" + ln.target, 
							source: ln.source, 
							target: ln.target,
							value: ln.value,
							cluster: ln.cluster})
		})
	};
	
	// set up the simulation and event to update locations after each tick
	const initializeSimulation = () => {
	  simulation.nodes(nodes);
	  simulation.on("tick", ticked);
	};
	
	// add forces to the simulation
	const initializeForces = () => {
		// add forces and associate each with a name
		simulation
			.force("link", d3.forceLink())
			.force("charge", d3.forceManyBody())
			.force("collide", d3.forceCollide())
			.force("center", d3.forceCenter())
			.force("forceX", d3.forceX())
			.force("forceY", d3.forceY());
		// apply properties to each of the forces
		updateForces();
		zoom_handler(svg);  
	};
	
	
	const initializeNodeDisplay = () => {
	  // set the data and properties of node circles
		node = g.append("g")
				.attr("class", "nodes")
				.selectAll("g")
				.data(nodes)
				.enter().append("g")
				.attr("name",function(d){ return d.name })
				.attr("id",function(d){ return "node_"+d.id });
				
		circles = node.append("circle")
				.attr("class","circle1")
				.attr("id",function(d){ return d.id })
			    .attr("name",function(d){ return d.name })
			    .attr("cluster",function(d){ return d.cluster })
				.attr("r", 5)
			    .attr("fill", "black")
			    .attr("stroke-opacity", 1)
			    .call(d3.drag()
				  .on("start", dragstarted)
				  .on("drag", dragged)
				  .on("end", dragended))
				.on("mouseover",function(d){
					d3.select(this).transition().duration(50).attr('r',8);
				})
				.on("mouseout",function(d){
					d3.select(this).transition().duration(50).attr('r',5);
				})
			    .on("click",function(d){
					ClickedNode( d3.select(this).attr("id"));					
				});

	  // node tooltip
	  node.append("title")
		  .text(function(d) { return d.name; });
	  
	  // visualize the graph
		labels = node.append("text")
			  .text(function(d) {
				return d.name;
			  })
			  .attr("font-size",8)
			  .attr('x', 8)
			  .attr('y', 6);
	};

	const ClickedNode = (selID) => {
		//reset variables, coloring		
		dispatcher(pathdeleteActionCreator({}))
		dispatcher(resetvalsActionCreator({}))
		dispatcher(snaselectActionCreator({selectedNode: selID}))
	
		d3.selectAll(".snalinks")._groups[0].forEach(dlk => {	
			dlk.style.stroke = SNAstate.linkColor; //reset color
		})
		d3.selectAll(".circle1").attr("fill","black").attr("r",5); 
		
		if (SNAstate.secondaryNode){
			d3.select("#" + SNAstate.secondaryNode).attr("fill","lightgreen");
		}
		d3.select("#"+selID).transition().duration(50).attr('fill','yellow');

	};

	// generate the svg objects and force simulation
	const initializeLinkDisplay = () => {

		  // set the data and properties of link lines
		  link = g.append("g")
					.attr("id","linksg")
					.selectAll("line")
					.data(Revlinks)
					.enter().append("line")
					.attr("class", "snalinks")
					.attr("stroke-opacity", 1)
					.attr("stroke-width", 2)
					.attr("id",function(d,i){ return d.source + "--" + d.target; })
					.attr("title",function(d){return "Title"})
					.attr("source",function(d){ return d.source })
					.attr("target",function(d){ return d.target })
					.attr("cluster",function(d){ return d.cluster })
					.attr("stroke-opacity",d => {return 1- 1/(+d.value)})
					.attr("data-tip","")
					.attr("data-for","registerTip")
					.on("mouseover",function(d){
						d3.select(this).transition().duration(50).attr('stroke-width',5);
					})
					.on("mouseout",function(d){
						d3.select(this).transition().duration(20).attr('stroke-width',2);
					});

		  link.append("title")
			  .text(function(d) { //console.log(d3.select(this)._groups[0][0].__data__); 
					return d.source + " - " + d.target; 
			  }) 	
		  
	};	
	
	// apply new force properties
	const updateForces = () => {
		// get each force by name and update the properties
		simulation.force("center")
			.x(width * forceProperties.center.x)
			.y(height * forceProperties.center.y);
		simulation.force("charge")
			.strength(forceProperties.charge.strength * forceProperties.charge.enabled)
			.distanceMin(forceProperties.charge.distanceMin)
			.distanceMax(forceProperties.charge.distanceMax);
		simulation.force("collide")
			.strength(forceProperties.collide.strength * forceProperties.collide.enabled)
			.radius(forceProperties.collide.radius)
			.iterations(forceProperties.collide.iterations);
		simulation.force("forceX")
			.strength(forceProperties.forceX.strength * forceProperties.forceX.enabled)
			.x(width * forceProperties.forceX.x);
		simulation.force("forceY")
			.strength(forceProperties.forceY.strength * forceProperties.forceY.enabled)
			.y(height * forceProperties.forceY.y);
		simulation.force("link")
			.id(function(d) { return d.id;})
			.distance(forceProperties.link.distance)
			.iterations(forceProperties.link.iterations)
			.links(forceProperties.link.enabled ? Revlinks : []);

		// updates ignored until this is run
		// restarts the simulation (important if simulation has already slowed down)
		simulation.alpha(1).restart();

	};
	
	const rerun = () => {

		initializeLinkDisplay();
		initializeNodeDisplay();
		initializeForces();
		updateForces();
		
		dispatcher(snavarsActionCreator({
						refreshData: false,
						nodeColor:SNAstate.nodeColor, 
						linkColor:SNAstate.linkColor, 
						isHighlight: SNAstate.isHighlight,
				}))
	};		
	

	return(
		<div id="SVGContainer" ></div>
	)
};

/////variables///////
let width = 850,
	height = 750;
let svg;
let g; 
let labels;
let Revlinks = []; //holding array for formatted link data

// values for all forces
var forceProperties = {
    center: {
        x: 0.5,
        y: 0.5
    },
    charge: {
        enabled: true,
        strength: -30,
        distanceMin: 1,
        distanceMax: 1000
    },
    collide: {
        enabled: true,
        strength: .7,
        iterations: 1,
        radius: 5
    },
    forceX: {
        enabled: false,
        strength: .1,
        x: .5
    },
    forceY: {
        enabled: false,
        strength: .1,
        y: .5
    },
    link: {
        enabled: true,
        distance: 30,
        iterations: 1
    }
}

// svg objects
var link, node, circles;

// force simulation
var simulation = d3.forceSimulation();

// update the display positions after each simulation tick
const ticked = () => {

		link
			.attr("x1", function(d) { return d.source.x; })
			.attr("y1", function(d) { return d.source.y; })
			.attr("x2", function(d) { return d.target.x; })
			.attr("y2", function(d) { return d.target.y; });

		node
			.attr("cx", function(d) { return d.x; })
			.attr("cy", function(d) { return d.y; });

		circles
			.attr("cx", function(d) { return d.x; })
			.attr("cy", function(d) { return d.y; });

		labels
			.attr("x", function(d) { return d.x+4; })
			.attr("y", function(d) { return d.y-3; });

		d3.select('#alpha_value').style('flex-basis', (simulation.alpha()*100) + '%');

}

//////////// UI EVENTS ////////////

function dragstarted(event,d) {
  if (!event.active) simulation.alphaTarget(0.1).restart();
  d.fx = d.x;
  d.fy = d.y;
}

function dragged(event,d) {
  d.fx = event.x;
  d.fy = event.y;
}

function dragended(event,d) {
  if (!event.active) simulation.alphaTarget(0.0001);
  d.fx = null;
  d.fy = null;
}


//add zoom capabilities 
var zoom_handler = d3.zoom()
    .on("zoom", zoom_actions);

function zoom_actions(event){
    g.attr("transform", event.transform)
}

export default Network2D;