import React, {useState} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {snasecondaryActionCreator, 
		pathaddActionCreator, pathdeleteActionCreator, pathselectActionCreator} from '../sna_actions';
import Pathfind from '../functions/Pathfind';
import * as d3 from 'd3';


const ModalPathfind = () => {
	
	const [maxDepth, setMaxDepth] = useState(10);
	const dispatcher = useDispatch();
	const snanodes = useSelector((state) => state.sna_nodes);
	const snalinks = useSelector((state) => state.sna_links);
	const snavars = useSelector((state) => state.snavars);
	const snapaths = useSelector((state) => state.sna_paths);
	
	let scores = [];
	
	const DropdownChange = (e) => {
		try {
			if (snavars.secondaryNode){
				d3.select("#"+snavars.secondaryNode).attr("fill","black")
			}
			let val = snanodes[e.target.selectedIndex - 1];
			dispatcher(snasecondaryActionCreator({secondaryNode: val['id']}));
			d3.select("#"+val['id']).attr('fill','lightgreen');
		
		} catch(err){ console.log("Node does not exist")}
		
	}

	//
	const SubmitPaths = () => {
		if (snavars.selectedNode!=="" & snavars.secondaryNode!==""){
			let paths = Pathfind(snalinks,snavars.selectedNode, snavars.secondaryNode, maxDepth);
			
			dispatcher(pathdeleteActionCreator({}));
			paths.forEach((p,i) => {
				dispatcher(pathaddActionCreator({path: p}));
			})
			
			d3.selectAll(".snalinks")._groups[0].forEach(dlk => {	
				dlk.style.stroke = snavars.linkColor; //reset color
			})

			ScoresCreate()
		}
	}

	//path is selected, change link colors
	const selectedPath = (e) => {
		
		let pInt = e.target.attributes.value.value;
		dispatcher(pathselectActionCreator({pathInt: pInt}))
		
		snapaths[pInt].path.forEach((lk,i) => {
			d3.selectAll(".snalinks")._groups[0].forEach(dlk => {	
				if (i < snapaths[pInt].path.length){
					if (lk + "--" + snapaths[pInt].path[i+1] === dlk.attributes.id.value |  snapaths[pInt].path[i+1] + "--" + lk === dlk.attributes.id.value){
						dlk.style.stroke = "red";
					}
				}
			})
		})
	}
	
	const ScoresCreate = () => {
			//scores
		let pathscores = [];
		snapaths.forEach(path => {
			let ps = [];
			snalinks.forEach(lk => {
				if (path.indexOf(lk['source'])>-1 & path.indexOf(lk['target'])>-1 & Math.abs(path.indexOf(lk['source']) - path.indexOf(lk['target']))===1){
					//found link
					ps.push(lk['value1']);
				}
			})
			let score = 0;
			ps.forEach(p => {
				score += +p
			})
			
			pathscores.push(Math.round(score/ps.length,3));
		})
		console.log(pathscores);
		scores = pathscores;
		
	}
	
	const Listvalues = () => {
		if (snalinks.length > 1){
			return(		
				<div>
					<input type="number" name="max_depth" default="10" min="1" max="50" value={maxDepth} onChange={e=>setMaxDepth(e.target.value)} />
					<br/>
					
					{/* dropdown */}
					<select id="modaldropdown" onChange={e=>DropdownChange(e)} >		
						<option key='1'  >
							Select A Node
						</option>
						{
							snanodes.map((d,i) => {
								return(
									<option key={"b"+i} value = {d['id']} >
										{d.id}
									</option>
								)
							})
						}
					</select>
					<button type="submit" onClick={SubmitPaths}> Submit </button>
				</div>
			)
		} else {
			return(<div></div>)
		}
	}
	
	//add path strings to modal for selection
	const Pathing = () => {

		if (snapaths.length > 0){
			if (snapaths[0].path !==""){
				let strings = snapaths.map((pth,i) => { 
					let str1 = "Path " + i + " ("+ pth.path.length +") [" + scores[i] + "] :   "
					pth.path.forEach((p,i) => { if (i<8){ str1 = str1 + p + " - " } })
					return(str1)
				})

				return(
					<div>
						{
							strings.map((ss,i) => (
								<div key={i} className="modal_pathway" value={i} onClick={e => selectedPath(e)}>
									{ss.substring(0,80)} ...
								</div>
							))
						}
					</div>
				)
			} else {
				return(<div></div>)
			}
		} else {
			return(<div>No Paths</div>)
		}
	}

	return (
		<div className="modalPopup">
			Select Secondary Node
			<br />
			Primary Node:  {snavars.selectedNode}
			<Listvalues />
			<Pathing />
		</div>
	)
};

export default ModalPathfind;