import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import {Todo} from "./type";
import { v1 as uuid } from 'uuid';


/////////
const todoInitialState: Todo[] = [
	{
		id: '1',
		desc: "Eaich treoi",
		isComplete: true
	},
	{
		id: '2',
		desc: "etijasfni",
		isComplete: false
	}
];


export const todoSlice = createSlice({
	name: 'todos_action',
	initialState: todoInitialState,
	reducers: {
	
		//itemname: function or object
		//function version
		edit: (state, {payload}: PayloadAction<{id: string, desc: string}>) => {
			const todoToEdit = state.find(todo => todo.id === payload.id);
			if (todoToEdit){
				//immer import allows mutation of state (redux toolkit only)
				todoToEdit.desc = payload.desc;
			}
		},
		toggle: (state, {payload}: PayloadAction<{id: string, isComplete: boolean }>) => {
			const todoToEdit = state.find(todo => todo.id === payload.id);
			if (todoToEdit){
				//immer import allows mutation of state (redux toolkit only)
				todoToEdit.isComplete = payload.isComplete;
			}		
		},
		remove: (state, {payload}: PayloadAction<{id: string}>) => {
			const index = state.findIndex(todo => todo.id === payload.id);
			if (index !== -1){
				//immer import allows mutation of state (redux toolkit only)
				state.splice(index,1);
			}			
		},
		//object version
		create: {
			reducer: (state, {payload}: PayloadAction<{id: string, desc: string, isComplete: boolean}>) => {
				state.push() //uses immer mutability
			},
			prepare: ({desc}:{ desc: string }) => ({ //prepares payload allowing custom functions
				payload: {
					id: uuid(),
					desc: desc,
					isComplete: false
				}
			})
		}
		//default case not necessary
	}
});


export const selectedTodoSlice = createSlice({
	name: 'selectedTodo_action',
	initialState: null as string | null,
	reducers: {
		select: (state, {payload}: PayloadAction<{id: string}>) => {
			//uses primitive values (null, string, int, etc.), so can't mutate state directly
			//just return completely new state
			return payload.id
		}
	}
});

//
export const counterSlice = createSlice({
	name: 'counter_action',
	initialState: 0,
	reducers: {},
	//to accept other state's actions
	extraReducers: {
		[todoSlice.actions.create.type]: state => state + 1,
		[todoSlice.actions.edit.type]: state => state + 1,
		[todoSlice.actions.remove.type]: state => state + 1,
		[todoSlice.actions.toggle.type]: state => state + 1,
	}
});

//manually export action creators
export const {
	create: createTodoActionCreator,
	edit: editTodoActionCreator,
	toggle: toggleTodoActionCreator,
	remove: deleteTodoActionCreator,
} = todoSlice.actions;
export const {
	select: selectTodoActionCreator,
} = selectedTodoSlice.actions;
