import React, {useState, ChangeEvent} from 'react';
import axios from 'axios';
//import { empdataActionCreator } from './data_actions';
//import { useDispatch } from 'react-redux';
import {ReviewModel_Review, AxiosPost} from "../../type";
//import {Review} from "../../type";

const ReviewImport = () => {

	const [formData, setFormData] = useState({
		modelname: 'xx'
	});
	const { modelname } = formData;
	//const [revData, setrevData] = useState<Review[]>();
	
	const handleFormChange = (e: ChangeEvent<HTMLInputElement>): void => {
		setFormData({modelname: e.target.value});
	};
	interface ModelDataType {
		label: string;
		value: string;
	};
	
	const dataset: ModelDataType[] = []
				
	const config = {
		headers: {
			'Content-Type': 'application/json'
		}
	};

	//------list of models
    const reviewmodelretrieve_post = () => {

		//axios call
        axios.post<ReviewModel_Review[]>(`${process.env.REACT_APP_API_URL}/api/reviews/models/`, {modelname}, config)
        .then(res => {
			res.data.forEach(d => {
				dataset.push({label : d.ModelKey, value: d.ModelType})
			})
			console.log(dataset)
        })
        .catch(err => {
			console.log('data error');
        })
    };
	
	//------chosen model data
    const reviewretrieve_post = () => {
		
		reviewmodelretrieve_post()
		
		//axios call
        axios.post<AxiosPost[]>(`${process.env.REACT_APP_API_URL}/api/reviews/review/`, {modelname}, config)
        .then((res) => {
			//let xx:any = res.data[0];
			//let rr: Review[] = xx.reviews;
		    //setrevData(rr);
        })
        .catch(err => {
			console.log('data error');
        })
    };

    return (
       <div>
		   Data Page
		  
			<input
				type="text"
				value={ modelname }
				onChange = { handleFormChange }
			/>
			<button type="submit" onClick={reviewretrieve_post}>Get Data</button>

	   </div>
    );
};


export default ReviewImport;
