import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import {Emp} from "../../type";


/////////
const empStateInit: Emp[] = [
	{
		name: '',
		empid: '',
		location: '',
	},
];


export const empdataSlice = createSlice({
	name: 'empdata_action',
	initialState: empStateInit,
	reducers: {
	
		load_data: {
			//uses primitive values (null, string, int, etc.), so can't mutate state directly
			//just return completely new state
			reducer: (state, {payload}: PayloadAction<{name: string, empid: string, location: string}>) => {
				state.push() //uses immer mutability
			},
			prepare: ({name, empid, location}:{ name: string, empid: string, location: string }) => ({ //prepares payload allowing custom functions
				payload: {
					name: name,
					empid: empid,
					location: location
				}
			})
		}
	}
});

//manually export action creators
export const {
	load_data: empdataActionCreator,
} = empdataSlice.actions;