import React, {useState, ChangeEvent} from 'react';
import axios from 'axios';
import {ReviewBigram, ReviewModel_Review, ReviewModel_Bigram, Review} from "../../type";
import {snarefreshDataActionCreator,
		bigramdeleteActionCreator, bigramDataActionCreator,
		links_createActionCreator, links_deleteActionCreator,
		linksfull_createActionCreator, linksfull_deleteActionCreator,
		nodes_createActionCreator, nodes_deleteActionCreator,
		nodesfull_createActionCreator, nodesfull_deleteActionCreator,
		snamodelActionCreator, reviewDataActionCreator, 
		reviewdeleteActionCreator, pathdeleteActionCreator,
		numclustersActionCreator, itemSelectActionCreator} from "./sna_actions";
import Clusters from './functions/Clusters';
import {Modal} from 'react-bootstrap'; 
import {useSelector, useDispatch} from 'react-redux';
import {SNA_Node, SNA_Link} from '../../type';
import ReviewItemsDropdown from "./modals/shared items/ReviewItemsDropdown"; 
import CleanString from './functions/CleanString';

const SNAImport = () => {

	//const snavars: SNAvars = useSelector((state: any) => state.snavars);
	const reviews: Review[] = useSelector((state: any) => state.reviewdata);
	const snaNodes: SNA_Node[] = useSelector((state: any) => state.sna_fullnodes);
	const snaLinks: SNA_Link[] = useSelector((state: any) => state.sna_fulllinks);
	const [formData, setFormData] = useState({  modelname: 'tt' });
	const [isModal0Visible,setIsModal0Visible] = useState(false);
	const clicked0 = () => { setIsModal0Visible(true) };
	const close0 = () => { setIsModal0Visible(false) };

	interface ModelDataType {
		label: string;
		value: string;
	};

	const [datamodels, setDatamodels] = useState<ModelDataType[]>([])
	const [callData, setCallData] = useState(false)  //to prevent rerun of model import
	const { modelname } = formData;
	const dispatcher = useDispatch();
	const Items: any = [];

	const config = {
		headers: {
			'Content-Type': 'application/json'
		}
	};

	const DropdownModelChg = (e: ChangeEvent<EventTarget>):void => {
		console.log(e)
		let dd: any = document.getElementById("dropdown_models");
		console.log(dd.options[dd.selectedIndex].text);
		setFormData({modelname: dd.options[dd.selectedIndex].text});
	};

	const itemSelect = (e: ChangeEvent<HTMLSelectElement>) => {
		if (Items.length < 1){
			reviews.map((d,i) => {
					if (Items.indexOf(d.ReviewItem)=== -1){
						Items.push(d.ReviewItem);
					}
			});
		};
		dispatcher(itemSelectActionCreator({ selectedItem: Items[e.target.selectedIndex - 1] }));
		itemFilter(Items[e.target.selectedIndex - 1]);	
	};
	
	
	const itemFilter = (item: string) => {
		//create items list - only provided with selectedindex from child node
		let desc_str: string = "";
		reviews.forEach(rv => {
			if (rv.ReviewItem===item){
				console.log(rv.Description);
				desc_str = desc_str + String(rv.Description)
			};
		});
		dispatcher(nodes_deleteActionCreator({}));
		dispatcher(links_deleteActionCreator({}));
		
		let terms = CleanString(desc_str).split(" ");
		console.log(item);
		console.log(terms);


		//apply full nodes/links to sample nodes/links
		snaNodes.forEach((d:SNA_Node) => {
			if (terms.indexOf(d['id'])>-1){
				dispatcher(nodes_createActionCreator(d));
			};	
		});
 		snaLinks.forEach((d:SNA_Link) => {
			if ( (terms.indexOf(d['source'])>-1 && terms.indexOf(d['target'])>-1)){
				dispatcher(links_createActionCreator(d));
			};
		}); 
		//reset 'refresh' to true
		dispatcher(snarefreshDataActionCreator({refreshData: true}))
	};

	//------list of models
    const reviewmodelretrieve = () => {
		setCallData(true); //only run once

		//axios call
        axios.post<ReviewModel_Review[]>(`${process.env.REACT_APP_API_URL}/api/reviews/models/`, {modelname}, config)
        .then(res => {

			const dataset: ModelDataType[] = []
			res.data.forEach(d => {
				dataset.push({label : d.ModelKey, value: d.ModelType})
			})
			setDatamodels(dataset);
        })
        .catch(err => {
			console.log('data error - Model');
        })
    };
	

	//------chosen model data
    const reviews_retrieve = () => {
		
		dispatcher(snamodelActionCreator({modelName: modelname}));
		//need to delete previous instance
		dispatcher(nodesfull_deleteActionCreator({}))
		dispatcher(linksfull_deleteActionCreator({}))
		dispatcher(pathdeleteActionCreator({}))
		dispatcher(reviewdeleteActionCreator({}))
		dispatcher(bigramdeleteActionCreator({}))
		dispatcher(nodes_deleteActionCreator({}));
		dispatcher(links_deleteActionCreator({}));
				
		//axios call - reviews from particular model
	     axios.post<ReviewModel_Review[]>(`${process.env.REACT_APP_API_URL}/api/reviews/review/`, {modelname}, config)
         .then(res => {	
			const dataset: ReviewModel_Review[] = res.data
			const dset: Review[] = dataset[0].reviews
			dset.forEach(rv => {
				dispatcher(reviewDataActionCreator(rv))
			})
		 })
		 .catch(err => {
			console.log('data error - Review');
         });
		 
		 
		//axios call - review bigrams for particular model
		axios.post<ReviewModel_Bigram[]>(`${process.env.REACT_APP_API_URL}/api/reviews/bigram/`, {modelname}, config)
        .then(res => {
			
			const dataset: ReviewModel_Bigram[] = res.data
			const dset: ReviewBigram[] = dataset[0].bigrams
			console.log(dset)
			let nn:any = getNodesfromLinks(dset,"Bigram_Term1","Bigram_Term2")
			let nn_clust = Clusters(nn,dset)
			let nodenames = nn_clust.map(n => n['id']);
			
			//nodes----------
			let clustnum:string[] = [];
			nn_clust.forEach((d:any) => {
				//store all nodes or just a portion
				let nod: SNA_Node = {
					id: d['id'],
					name: d['id'],
					cluster: d['cluster']
				}
				dispatcher(nodesfull_createActionCreator(nod))
				dispatcher(nodes_createActionCreator(nod))
				if (clustnum.indexOf(d['cluster'])===-1){
					clustnum.push(d['cluster']);
				};
			});
			dispatcher(numclustersActionCreator({numClust: clustnum.length}))			
			
			//links----
			dset.forEach(d => {
			    if (d['Bigram_Term1'] !== d['Bigram_Term2']){  //prevent single loops				
					let clust: number = nn_clust[nodenames.indexOf(d['Bigram_Term1'])]['cluster']
					let lnk: SNA_Link = {
						modelid: String(d["ModelKey"]), 
						id: String(d["id"]), 
						source: d["Bigram_Term1"], 
						target: d["Bigram_Term2"],
						cluster: clust,
						value1: d["Score"],
						value2: d["Distance"],
					}
					dispatcher(bigramDataActionCreator(d));				
					//store all links or just a portion
					dispatcher(linksfull_createActionCreator(lnk));
					dispatcher(links_createActionCreator(lnk));
				}
			}) 
			//itemFilter()	
		//reset 'refresh' to true
		dispatcher(snarefreshDataActionCreator({refreshData: true}))			
        })
		.catch(err => {
			console.log('data error - Bigram');
        });
    };

	const DeleteModel = () => {

		axios.post(`${process.env.REACT_APP_API_URL}/api/reviews/delete/`, {modelname}, config)
        .then(res => {
			console.log('deleted ' + modelname);
        })
        .catch(err => {
			console.log('data error - Model');
        });
	
	};

	if (callData===false){
		reviewmodelretrieve() //run just once on open
	};
    return (
       <div>
			<div onChange={e=>DropdownModelChg(e)} >
				<select id="dropdown_models" >		
							<option key='1'>
								Select Model Name
							</option>
							{
								datamodels.map((d,i) => {
									return(
										<option key={"b"+i}  value={d.label}>
											{d.label}
										</option>
									)
								})
							}
				</select>
				<button type="submit" onClick={reviews_retrieve}>Get Data</button>
			</div>
			<ReviewItemsDropdown change={itemSelect}/>
			<br />

			<div onClick={clicked0} style={{'border': '2px solid', 'width': '150px', 'marginTop':'3px', 'cursor':'pointer'}} > 
				Delete selected? 
			</div>
			<Modal show={isModal0Visible} size='sm' >
				<div style={{color: 'red'}}>
					This will permanently delete the model from the data
					<br />
					<button type="submit" onClick={DeleteModel}> Delete </button>
					<button onClick={close0}> Close </button>
				</div>
			</Modal>
	   </div>
    );
};

export default SNAImport;

//////////helper functions/////////////
///get nodes from links
const getNodesfromLinks = (data: any, linkfieldName1: string, linkfieldName2: string) => {
	let new_nodes: any = [];
	let uniquevals: any = [];
	data.forEach((d:any) => {
		if (uniquevals.indexOf(d[linkfieldName1])===-1){
			uniquevals.push(d[linkfieldName1])
			new_nodes.push({Name: d[linkfieldName1], id: d[linkfieldName1]})
		}
		if (uniquevals.indexOf(d[linkfieldName2])===-1){
			uniquevals.push(d[linkfieldName2])
			new_nodes.push({Name: d[linkfieldName2], id: d[linkfieldName2]})
		}
	})
	
	return new_nodes;
};