import React, {useState} from 'react';
import {useSelector} from 'react-redux';
import * as d3 from 'd3';

const ModalNearbyCommunity = () => {

	const [radius, setRadius] = useState(15)
	const snavars = useSelector((state) => state.snavars);

	const applyNeighborhood = () => {
		
		if (snavars.selectedNode!==''){
			
			let svg1 = d3.select("#" + snavars.selectedNode)._groups[0]
			let ax = svg1[0].__data__.x;
			let ay = svg1[0].__data__.y;
			
			let parentnode = d3.select("#node_"+snavars.selectedNode)
			parentnode.lower();
			
			let xx = parentnode.append("circle")
				.attr("class","circle2")
				.attr("id","neighborhood_circle")
				.attr("r", radius * 10)
			    .attr("fill", "grey")
			    .attr("fill-opacity", .3)
				.attr("cx",ax)
				.attr("cy",ay);
			xx.lower();
			
			//nodesinrange
			d3.selectAll(".circle1")._groups[0].forEach(node => {
				let x = node.attributes.cx.value
				let y = node.attributes.cy.value
				let dist = Math.pow(Math.pow(x - ax,2)+Math.pow(y-ay,2),.5)
				if (dist < radius * 10){
					console.log(node.attributes.name.value + " " + dist)
				}
			})
			
		}
	}
	
	const clearCircle = () => {
		d3.selectAll("#neighborhood_circle").remove();
			
	}
	
	return(
		<div className="modalPopup">
			Selected Node: {snavars.selectedNode}
			<br />
			radius: <input type="number" name="comm_radius" default="15" min="1" max="50" value={radius} onChange={e=>setRadius(e.target.value)} />
			<button onClick={applyNeighborhood}>Submit </button>
			<div stlye={{height:'20px', width: '100%'}}></div>
			<button onClick={clearCircle}> Remove </button>
		</div>
	)
};

export default ModalNearbyCommunity;