import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { snavarsActionCreator } from '../sna_actions';
import  {SwatchesPicker} from 'react-color';
import * as d3 from 'd3';


const ModalNodeColor = () => {
	
	const SNAstate = useSelector((state) => state.snavars);

	const dispatcher = useDispatch();
	
	const changeVal = (e) => {

		d3.selectAll(".circle1")._groups[0].forEach(cir => {
			cir.attributes.style.value = "stroke: " + e.hex;
		})
		
		dispatcher(snavarsActionCreator({
						refresh: SNAstate.refresh,
						nodeColor:e.hex, 
						linkColor:SNAstate.linkColor, 
						isHighlight: SNAstate.isHighlight
		}))
	};

	return (
		<div className="modalPopup">
			Click to Change Node Color
			<SwatchesPicker onChangeComplete={e=> changeVal(e)}/>
		</div>
	)
};

export default ModalNodeColor;