import React, {useState} from 'react';
import { useSelector } from 'react-redux';
import * as d3 from 'd3';
import ReviewItemsDropdown from "./shared items/ReviewItemsDropdown";
import CleanString from "../functions/CleanString";


const ModalLinkcolor = () => {
	
	const Reviews = useSelector((state) => state.reviewdata);
	const SNANodes = useSelector((state) => state.sna_nodes);
	const SNALinks = useSelector((state) => state.sna_links);

	const [selectedItem, setSelectedItem] = useState('');
	//const [selectedReview, setSelectedReview] = useState('');
	let Items = [];

	function DropdownChg(e){
		if (Items.length < 1){
			Reviews.map((d,i) => {
					if (Items.indexOf(d.ReviewItem)=== -1){
						Items.push(d.ReviewItem);
					}
			});
		}
		setSelectedItem(Items[e.target.selectedIndex - 1]);

	};

	const EnumerateReviews = () => {
		
		return (
			<div style={{marginTop: "15px"}}> 
				{	
					Reviews.map((rev,i) => {
						if (rev.ReviewItem === selectedItem){
						let str1 = rev.Summary	
						if (rev.Summary.length > 30){ let str1 = rev.Summary.substring(0,30)+"..."} 
							return(
								<div className="Modal_ReviewItems" key={i} value={i} onClick={e=> ClickReview(e)} >
									Review  {i}: 
								
									Score: {rev.Rating} - Summary: {str1} 
									
								</div>
							)
						}
					})
				}
			</div>
		)
	}

	const ClickReview = (e) => {
		let xx = e.target.attributes.value.value
		let terms = CleanString(Reviews[xx].Description).split(" ");
		let foundNodes = [];
		
		SNANodes.forEach(nd => {
			d3.select("#"+nd['id']).attr('fill','black'); //reset to black
			if (terms.indexOf(nd['id']) > -1){
				foundNodes.push(nd['id']);
			}
		})
		console.log(foundNodes)
		foundNodes.forEach(nd => {
			d3.select("#"+nd).attr('fill','orange').attr("r",10);
		})
		
		let foundLinks = [];
		SNALinks.forEach(lk => {
			//not directly next to, but if both are contained
			if (foundNodes.indexOf(lk['source'])>-1 & foundNodes.indexOf(lk['target'])>-1){
				console.log("found link - " + lk['source'] + "-" + lk['target'])
				//is directly next to
				if (Math.abs(foundNodes.indexOf(lk['source']) - foundNodes.indexOf(lk['target'])) === 1){
					console.log("direct " + lk['source'] + "-" + lk['target'])
					foundLinks.push(lk);
				}
			}
			
		})
		console.log(foundLinks); 
		
		//add links to diagram?
		//console.log(d3.select("#linksg"))
		
	}

	return (
		<div className="modalPopup">
			Items: 
			<ReviewItemsDropdown change={e => DropdownChg(e)} />

			<br />
			<EnumerateReviews />
		</div>
	)
};

export default ModalLinkcolor;