import {useState} from 'react';
import {useSelector} from 'react-redux';
//import {nodes_deleteActionCreator, links_deleteActionCreator} from '../sna_actions';
import * as d3 from 'd3';

const ModalClusters = () => {
	//const snanodes = useSelector((state) => state.sna_nodes);
	//const snalinks = useSelector((state) => state.sna_links);
	const snavars = useSelector((state) => state.snavars);
	const [hasbeenrun, setHasbeenrun] = useState(false);
	const [colors, setColors] = useState([]);

	//let dispatcher = useDispatch();
	
	if (hasbeenrun === false){
		let colors1 = [];
		for (var c=1; c<=snavars.numClust; c++){
			colors1.push("rgb(" + randomint() + "," + randomint() + "," + randomint() + ")");
		}
		setColors(colors1);
		setHasbeenrun(true);
	}
	const runClustColors = () => {
		
		d3.selectAll(".circle1")._groups[0].forEach(cir => {
			cir.attributes.style.value = "stroke: "+ colors[cir.attributes.cluster.value];
		});
		
	};
	
	const DropdownChange = (e) => {

		d3.selectAll(".circle1")._groups[0].forEach(cir => {
			if (cir.attributes.cluster.value !== e.target.value){
				cir.attributes.style.value = "stroke: #FFFFFF";
			} else {
				cir.attributes.style.value = "stroke: " + colors[cir.attributes.cluster.value];
			}
		});
		d3.selectAll(".snalinks")._groups[0].forEach(lnk => {
			if (lnk.attributes.cluster.value !== e.target.value){
				lnk.attributes.style.value = "stroke: #FFFFFF";
			} else {
				lnk.attributes.style.value = "stroke: " + snavars.linkColor;
			}
		});	 
		
	};
	
	return(
		<div className="modalPopup">
			Color Nodes by Cluster
			<button onClick={runClustColors}>Color Nodes</button>
			
			<div> Filter Cluster </div>
			<select id="modaldropdown" onChange={e=>DropdownChange(e)} >		
					<option key='1'  >
						Select A Node
					</option>
					{
						colors.map((d,i) => {
							return(
								<option key={"c"+i} value = {i} style={{color: colors[i]}}>
									Cluster {i}
								</option>
							)
						})
					}
			</select>
			<button >Select</button>
		</div>
	)
};
export default ModalClusters;


function randomint(){
	return Math.floor(Math.random()*255);
}