import React from 'react';
import SNASidebar from "./SNAsidebar";
import Network2D from "./Network2D";
import {useSelector } from 'react-redux';

import './css/snaCSS.css';

const SNABase = () => {
	
	//need tsx notation
	let SNAstate = useSelector((state: any) => state.snavars);

    return (
		
       <div id="networkpage">
			<SNASidebar />
				<div className="importbar"> 
					<h2>Social Network Analysis: {SNAstate.modelName} </h2> 
					
					
				</div>
			<Network2D />
	   </div>
    );
};


export default SNABase;
