import {useState} from 'react';
import { ProSidebar, Menu, MenuItem, SubMenu, SidebarHeader } from 'react-pro-sidebar';
import 'react-pro-sidebar/dist/css/styles.css';
import './css/sidebar.css'
//import {Link} from 'react-router-dom';
import {Modal} from 'react-bootstrap';
import ModalNodeColor from './modals/ModalNodeColor';
import ModalLinkColor from './modals/ModalLinkColor';
import ModalPathfind from './modals/ModalPathfind';
import ModalNearbyCommunity from './modals/ModalNearbyCommunity';
import ModalImport from './modals/ModalImport';
import ModalReview from './modals/ModalReview';
import ModalScores from './modals/ModalScores';
import ModalClusters from './modals/ModalClusters';
import ModalNodeDropdown from './modals/ModalNodeDropdown';


const SNASidebar = () => {

	let [isModal0Visible,setIsModal0Visible] = useState(false);
	let [isModal1Visible,setIsModal1Visible] = useState(false);
	let [isModal2Visible,setIsModal2Visible] = useState(false);
	let [isModal3Visible,setIsModal3Visible] = useState(false);
	let [isModal4Visible,setIsModal4Visible] = useState(false);
	let [isModal5Visible,setIsModal5Visible] = useState(false);
	let [isModal6Visible,setIsModal6Visible] = useState(false);
	let [isModal7Visible,setIsModal7Visible] = useState(false);
	let [isModal8Visible,setIsModal8Visible] = useState(false);


	const clicked0 = () => { setIsModal0Visible(true) };
	const close0 = () => { isModal0Visible ? setIsModal0Visible(false) : setIsModal0Visible(true) };	
	
	const clicked1 = () => { isModal1Visible ? setIsModal1Visible(false) : setIsModal1Visible(true) };
	const clicked2 = () => { isModal2Visible ? setIsModal2Visible(false) : setIsModal2Visible(true) };
	
	const clicked3 = () => { setIsModal3Visible(true) };
	const close3 = () => { setIsModal3Visible(false) };		
	
	const clicked4 = () => { setIsModal4Visible(true) };
	const close4 = () => { setIsModal4Visible(false) };
	
	const clicked5 = () => { setIsModal5Visible(true) };
	const close5 = () => { setIsModal5Visible(false) };
	
	const clicked6 = () => { setIsModal6Visible(true) };
	const close6 = () => { setIsModal6Visible(false) };

	const clicked7 = () => { setIsModal7Visible(true) };
	const close7 = () => { setIsModal7Visible(false) };
		
	const clicked8 = () => { setIsModal8Visible(true) };
	const close8 = () => { setIsModal8Visible(false) };
		
	return(
		<div className="sidebar">
			<ProSidebar>
			   <SidebarHeader>
					<div style={{color: 'white', marginLeft: '20px'}}> X Options </div>
			   </SidebarHeader>
			  <Menu iconShape="square">

				<div className="box" > 
					<MenuItem className="menu-item"  onClick = {clicked0} >
						Import Data Model
					</MenuItem>
					<Modal show={isModal0Visible} >
						<ModalImport />
					<button onClick={close0}> Close </button>
					</Modal>
				</div>
			  
				<div className="box"> 
					<SubMenu title="Color" >
						<MenuItem className="menu-item" onClick = {clicked1}>
							Node Color
							<Modal show={isModal1Visible} >
								<ModalNodeColor />
							</Modal>
						</MenuItem>
					
						<MenuItem className="menu-item" onClick = {clicked2} >
							Link Color
							<Modal show={isModal2Visible}>
								<ModalLinkColor />
							</Modal>
						</MenuItem>
					</SubMenu>
				</div>
				
				{/* Selected Actions Dropdown  */}
				<div className="box"> 
					<SubMenu title="Selected Actions" >
						<MenuItem className="menu-item" onClick = {clicked3} >
							Pathfinding
						</MenuItem>
						<Modal show={isModal3Visible} >
							<ModalPathfind />
							<button onClick={close3}> Close </button>
						</Modal>	
							
						<MenuItem className="menu-item" onClick = {clicked4} >
								Nearby Community
						</MenuItem>
						<Modal show={isModal4Visible} >
								<ModalNearbyCommunity />
								<button onClick={close4}> Close </button>
						</Modal>	

						<MenuItem className="menu-item" onClick = {clicked8} >
								Find Node
						</MenuItem>
						<Modal show={isModal8Visible} >
								<ModalNodeDropdown />
								<button onClick={close8}> Close </button>
						</Modal>				  
					</SubMenu>
				</div>
				
				<div className="box"> 
					<SubMenu title="Groupings" >
						<MenuItem className="menu-item" onClick = {clicked5} >
							Reviews
						</MenuItem>
						<Modal show={isModal5Visible} size={'lg'}>
							<ModalReview />
							<button onClick={close5}> Close </button>
						</Modal>	
					  
					    <MenuItem className="menu-item" onClick = {clicked6} >
							Scores
						</MenuItem>
						<Modal show={isModal6Visible} size={'md'}>
							<ModalScores />
							<button onClick={close6}> Close </button>
						</Modal>	

					    <MenuItem className="menu-item" onClick = {clicked7} >
							Clusters
						</MenuItem>
						<Modal show={isModal7Visible} size={'md'}>
							<ModalClusters />
							<button onClick={close7}> Close </button>
						</Modal>
					</SubMenu>
				</div>
				
			  </Menu>
			  	
			</ProSidebar>
		</div>
	)
};

export default SNASidebar;